body {
  --accent-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

ul {
  list-style: none;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 40px;
  padding: 10px 50px;
  display: flex;
}

.nav__logo {
  align-self: center;
  display: block;
}

.nav__list-items {
  text-align: center;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.nav__list-item:not(:last-child) {
  margin-right: 20px;
}

.nav__list-link {
  text-transform: uppercase;
  color: #000;
  text-align: center;
  align-content: center;
  height: 40px;
  padding: 12.5px 0;
  line-height: 14.6px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav__list-link:hover, .is-active {
  color: var(--accent-color);
}

.is-active:after {
  content: "";
  background-color: var(--accent-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 2.5px;
  left: 0;
  right: 0;
}
